.App{display:flex}@media screen and (max-width: 768px){.App{display:inline}}#side-nav-wrapper{margin-right:2em}@media screen and (max-width: 768px){#body-content-wrapper{display:flex;justify-content:center}#side-nav-wrapper .sub-nav-ul{padding-top:1em}}#side-nav-container{display:block;padding:1em;width:auto;height:auto;font-weight:bold}#side-nav-container ul{padding-top:0.5em;list-style-type:none}#side-nav-container ul li{padding:0.5em}#side-nav-container .link,#side-nav-container .btn{color:black;text-decoration:none;-webkit-transition:0.3s;transition:0.3s;font-size:18px;white-space:nowrap}#side-nav-container .link:hover,#side-nav-container .btn:hover{color:#04f}#side-nav-container .btn{border:none;padding:0;margin:0;font-weight:500}#side-nav-container .btn:focus{outline:0}#side-nav-container .active{color:#04f}@media screen and (max-width: 768px){#side-nav-container #side-nav-container{width:100%;padding:0;margin:0}#side-nav-container .link-container{padding-top:1em}#side-nav-container nav ul{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-between}#side-nav-container li{flex:1 0 40%}#side-nav-container .side-nav-ul{margin:0;padding:0}}

.individual-project-image-ul{list-style:none}.individual-project-image-li{padding:1em}.individual-project-image{width:100%;max-width:500px}.individual-project-info-container{margin-right:3em;max-width:500px}.individual-project-info-container .individual-project-info-name{font-weight:700;font-size:26px}.individual-project-info-container .individual-project-info-description,.individual-project-info-container .individual-project-info-year{font-size:18px;white-space:pre-wrap}

.about-text-container{margin:2em}.text{font-weight:bold;font-size:26px;padding-bottom:1em}.unicode{padding-right:1em}

canvas{background-color:white;width:50vw;height:100%}

.project-grid-container{margin:2em 1em;display:flex;flex-direction:row;flex-wrap:wrap;justify-content:center;width:100%}.project-grid-item{margin:1em}

.project-tile-container{border:2px solid black;width:250px;height:250px;position:relative;background-color:rgba(0,66,255,0)}.project-tile-container .project-tile-text-container{color:white;width:100%;height:100%;font-size:18px;position:absolute;opacity:0;overflow-wrap:break-word;-webkit-transform:translateY(-100%);transform:translateY(-100%);-webkit-transition:all 0.3s ease-in-out;transition:all 0.3s ease-in-out}.project-tile-container .project-tile-text-container:hover{opacity:1;background-color:rgba(0,66,255,0.8)}.project-tile-container:hover{border:2px solid #0042ff}.project-tile-image{height:100%;background-position:center;background-size:cover;background-repeat:no-repeat}.project-tile-text{padding:1em;font-weight:bold;font-size:24px;-webkit-touch-callout:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.project-tile-link{text-decoration:none;color:white}

a,button,.text{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.project-tile-link,.link{cursor:ne-resize}

body{margin:0;font-family:'Raleway', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;cursor:nw-resize}

